"use client";

import React from "react";
import {
  LayoutDashboard,
  Users,
  Wallet,
  Receipt,
  FileText,
} from "lucide-react";
import { cn } from "@/lib/utils";
import Link from "next/link";
import { usePathname } from 'next/navigation';

const Sidebar = () => {
  const pathname = usePathname();

  const routes = [
    {
      href: "/",
      label: "Dashboard",
      icon: LayoutDashboard,
    },
    {
      href: "/data-anggota",
      label: "Data Anggota",
      icon: Users,
    },
    {
      href: "/data-pinjaman",
      label: "Data Pinjaman",
      icon: Wallet,
    },
    {
      href: "/pembayaran",
      label: "Pembayaran",
      icon: Receipt,
    },
    {
      href: "/laporan",
      label: "Laporan",
      icon: FileText,
    },
  ];

  return (
    <div className="h-screen w-64 bg-[#1e1b4b] text-white">
      <div className="p-6">
        <h2 className="text-xl font-semibold mb-6">KSP Manager</h2>
        <nav className="space-y-1">
          {routes.map((route) => (
            <Link
              href={route.href}
              key={route.href}
              className={cn(
                "flex items-center gap-3 px-4 py-3 text-sm font-medium rounded-lg transition-colors",
                pathname === route.href
                  ? "bg-white/10 text-white"
                  : "text-gray-400 hover:bg-white/5 hover:text-white"
              )}
            >
              <route.icon className="h-5 w-5" />
              <span>{route.label}</span>
            </Link>
          ))}
        </nav>
      </div>
    </div>
  );
};

export default Sidebar;
